// ìˆ«ì í¬ë§·íŒ… í•¨ìˆ˜
export const formatNumber = (value: number | undefined | null): string => {
  if (value === undefined || value === null) return '0';
  return new Intl.NumberFormat('ko-KR', {
    maximumFractionDigits: 0
  }).format(value);
};

// í†µí™” í¬ë§·íŒ… í•¨ìˆ˜ (ì¡°Â·ì–µÂ·ì²œë§Œì›Â·ë°±ë§Œì› ë‹¨ìœ„ í•œêµ­í˜• í‘œê¸°)
export const formatCurrency = (value: number | undefined | null): string => {
  if (value === undefined || value === null || isNaN(value)) return '0ì–µ';

  const num = Math.abs(value);
  const sign = value < 0 ? '-' : '';
  const TRILLION = 1_000_000_000_000; // 1ì¡°
  const EOK = 100_000_000; // 1ì–µ

  // ì†Œìˆ˜ì  1ìë¦¬(ë¶ˆí•„ìš”í•˜ë©´ ì œê±°)
  const oneDecimal = (v: number) => {
    const str = v.toFixed(1);
    return str.endsWith('.0') ? str.slice(0, -2) : str;
  };

  if (num >= TRILLION) {
    const tril = Math.floor(num / TRILLION);            // ì¡° ë‹¨ìœ„
    const remainEok = Math.floor((num % TRILLION) / EOK); // ì–µ ë‹¨ìœ„ ì •ìˆ˜
    if (remainEok > 0) {
      return `${sign}${tril}ì¡° ${remainEok}ì–µ`;
    }
    return `${sign}${tril}ì¡°`;
  }

  // 1ì–µ ë¯¸ë§Œë„ ëª¨ë‘ ì–µ ë‹¨ìœ„ë¡œ í‘œê¸°
  const eokVal = num / EOK;
  return `${sign}${oneDecimal(eokVal)}ì–µ`;
};

// ì–µ ë‹¨ìœ„ ìˆ«ì í¬ë§· (ì°¨íŠ¸ìš©)
export const formatEok = (eokValue: number): string => {
  return `${formatNumber(eokValue)}ì–µ`;
};

// ë§¤ì¶œì•¡ì„ ì–µ ë‹¨ìœ„ë¡œ ë³€í™˜í•˜ëŠ” í•¨ìˆ˜
export const formatRevenue = (value: number | undefined | null): string => {
  if (value === undefined || value === null || isNaN(value)) return '0ì–µ';
  return `${(value / 100000000).toFixed(1)}ì–µ`;
};